import React, { Component } from 'react'
import threeEntryPoint from './threejs/threeEntryPoint'
import './App.css'

class App extends Component {
  componentDidMount () {
    threeEntryPoint(this.threeRootElement)
  }

  render () {
    return (
      <div className='App' ref={(e) => { this.threeRootElement = e }} />
    )
  }
}

export default App



